.row-header{
    margin:0px auto;
    padding:0px auto;
}

.row-content {
    margin:0px auto;
    padding: 50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height:400px;
    color: aliceblue;

}
.row1{
    color: aliceblue;
}
.footer{
    background-color: rgb(14, 10, 20);
    margin:0px auto;
    padding: 20px 0px 20px 0px;
}
.jumbotron {
    padding:70px 30px 70px 30px;
    margin:0px auto;
    background:url(/assets/images/farmer.png);
    color:rgb(253, 253, 253);
    font-weight:bold;
}
address{
    font-size:80%;
    margin:0px;
    color:#f8f8f8;
}
h6{
    color: slateblue;
}
h5,th,p1{
    color: aliceblue;
}
p{
    text-align: justify;
    color: aliceblue;

}
h4{
    color: slategrey;
}
h3,h1,h2{
    text-align: center;
    color: aliceblue;

}

.navbar-dark {
    background-color: rgb(0, 0, 0);
}
.page-enter {
    opacity: 0.01;
    transform: translateX(-100%);
}

.page-enter-active {
    opacity: 1;
    transform: translateX(0%);
    transition: all 300ms ease-in;
}

.page-exit {
    opacity: 1;
    transform: translateX(0%);
}

.page-exit-active {
    opacity: 0.01;
    transform: translateX(100%);
    transition: all 300ms ease-out;
}